import React, { useState, useEffect } from 'react';
import { User } from '@/services/api/users.service';
import { Empresa } from '@/services/api/empresa.service';
import { services } from '@/services/api';
import Input from '@/components/form/input/InputField';
import Select from '@/components/ui/Select';
import Button from '@/components/ui/button/Button';
import Label from '@/components/form/Label';
import toast from 'react-hot-toast';

type UsuarioFormProps = {
  usuario?: User | null;
  onSubmit: (data: any) => void;
  onCancel: () => void;
};

const UsuarioForm: React.FC<UsuarioFormProps> = ({
  usuario,
  onSubmit,
  onCancel
}) => {
  const [formData, setFormData] = useState({
    nome: '',
    email: '',
    senha: '',
    celular: '',
    role: 'VENDEDOR' as 'SUPERADMIN' | 'ADMIN' | 'GESTOR' | 'VENDEDOR' | 'CLIENTE',
    empresaId: ''
  });
  const [empresas, setEmpresas] = useState<Empresa[]>([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    loadEmpresas();
    if (usuario) {
      setFormData({
        nome: usuario.nome || '',
        email: usuario.email || '',
        senha: '',
        celular: usuario.celular || '',
        role: usuario.role,
        empresaId: usuario.empresaId?.toString() || ''
      });
    }
  }, [usuario]);

  const loadEmpresas = async () => {
    try {
      const response = await services.empresa.list({ perPage: 1000 });
      setEmpresas(response.data || []);
    } catch (error) {
      toast.error('Erro ao carregar empresas');
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      const data = {
        ...formData,
        empresaId: formData.empresaId ? parseInt(formData.empresaId) : undefined,
        senha: formData.senha || undefined
      };
      onSubmit(data);
    } catch (error) {
      toast.error('Erro ao processar formulário');
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const roleOptions = [
    { value: 'SUPERADMIN', label: 'Super Admin' },
    { value: 'ADMIN', label: 'Admin' },
    { value: 'GESTOR', label: 'Gestor' },
    { value: 'VENDEDOR', label: 'Vendedor' },
    { value: 'CLIENTE', label: 'Cliente' }
  ];

  const empresaOptions = empresas.map(empresa => ({
    value: empresa.id.toString(),
    label: empresa.nome
  }));

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <Label htmlFor="nome">Nome *</Label>
          <Input
            id="nome"
            value={formData.nome}
            onChange={(e) => handleChange('nome', e.target.value)}
            required
            placeholder="Digite o nome do usuário"
          />
        </div>
        <div>
          <Label htmlFor="email">Email *</Label>
          <Input
            id="email"
            type="email"
            value={formData.email}
            onChange={(e) => handleChange('email', e.target.value)}
            required
            placeholder="Digite o email"
          />
        </div>
        <div>
          <Label htmlFor="senha">{usuario ? 'Nova Senha' : 'Senha *'}</Label>
          <Input
            id="senha"
            type="password"
            value={formData.senha}
            onChange={(e) => handleChange('senha', e.target.value)}
            required={!usuario}
            placeholder="Digite a senha"
          />
        </div>
        <div>
          <Label htmlFor="celular">Celular</Label>
          <Input
            id="celular"
            value={formData.celular}
            onChange={(e) => handleChange('celular', e.target.value)}
            placeholder="Digite o celular"
          />
        </div>
        <div>
          <Label htmlFor="role">Função *</Label>
          <Select
            placeholder="Selecione a função"
            options={roleOptions}
            value={roleOptions.find(opt => opt.value === formData.role)}
            onChange={(option) => handleChange('role', (option as any)?.value || 'VENDEDOR')}
          />
        </div>
        <div>
          <Label htmlFor="empresaId">Empresa</Label>
          <Select
            placeholder="Selecione uma empresa"
            options={empresaOptions}
            value={empresaOptions.find(opt => opt.value === formData.empresaId)}
            onChange={(option) => handleChange('empresaId', (option as any)?.value || '')}
            isClearable
          />
        </div>
      </div>

      <div className="flex justify-end gap-3 pt-4">
        <Button
          type="button"
          variant="outline"
          onClick={onCancel}
          disabled={loading}
        >
          Cancelar
        </Button>
        <Button
          type="submit"
          variant="primary"
          disabled={loading}
        >
          {loading ? 'Salvando...' : usuario ? 'Atualizar' : 'Criar'}
        </Button>
      </div>
    </form>
  );
};

export default UsuarioForm;
