'use client';

import React, { useCallback, useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { services } from '@/services/api';
import { User } from '@/services/api/users.service';
import { formatDate } from '@/utils/format';
import { 
  UserGroupIcon, 
  PlusIcon,
  MagnifyingGlassIcon
} from '@heroicons/react/24/outline';
import Input from '@/components/form/input/InputField';
import Button from '@/components/ui/button/Button';
import Pagination from '@/components/common/Pagination';
import { Modal } from '@/components/ui/modal';
import UsuarioCard from '@/components/admin/UsuarioCard';
import UsuarioForm from '@/components/admin/UsuarioForm';
import Label from '@/components/form/Label';
import Select from '@/components/ui/Select';
import toast from 'react-hot-toast';

export default function UsuariosManagement() {
  const router = useRouter();
  const [usuarios, setUsuarios] = useState<User[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(9);
  const [roleFilter, setRoleFilter] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedUsuario, setSelectedUsuario] = useState<User | null>(null);

  const loadUsuarios = useCallback(async () => {
    try {
      setLoading(true);
      const response = await services.users.list({
        page: currentPage,
        limit: itemsPerPage,
        search: searchTerm || undefined,
        role: roleFilter || undefined
      });
      
      setUsuarios(response.data || []);
      setTotalPages(response.pagination?.totalPages || 1);
    } catch (error) {
      console.error('Erro ao carregar usuários:', error);
      toast.error('Erro ao carregar usuários');
    } finally {
      setLoading(false);
    }
  }, [currentPage, itemsPerPage, searchTerm, roleFilter]);

  useEffect(() => {
    loadUsuarios();
  }, [loadUsuarios]);

  const handleEdit = (usuario: User) => {
    setSelectedUsuario(usuario);
    setIsModalOpen(true);
  };

  const handleView = (usuario: User) => {
    // Implementar visualização detalhada
    toast.success(`Visualizando usuário: ${usuario.nome}`);
  };

  const handleSave = async (data: any) => {
    try {
      if (selectedUsuario) {
        await services.users.update(selectedUsuario.id, data);
        toast.success('Usuário atualizado com sucesso');
      } else {
        await services.users.create(data);
        toast.success('Usuário criado com sucesso');
      }
      setIsModalOpen(false);
      setSelectedUsuario(null);
      loadUsuarios();
    } catch (error) {
      console.error('Erro ao salvar usuário:', error);
      toast.error('Erro ao salvar usuário');
    }
  };

  const handleDelete = async (id: number) => {
    if (!confirm('Tem certeza que deseja excluir este usuário?')) {
      return;
    }

    try {
      await services.users.delete(id);
      toast.success('Usuário excluído com sucesso');
      loadUsuarios();
    } catch (error) {
      console.error('Erro ao excluir usuário:', error);
      toast.error('Erro ao excluir usuário');
    }
  };

  const roleOptions = [
    { value: '', label: 'Todas as funções' },
    { value: 'SUPERADMIN', label: 'Super Admin' },
    { value: 'ADMIN', label: 'Admin' },
    { value: 'GESTOR', label: 'Gestor' },
    { value: 'VENDEDOR', label: 'Vendedor' },
    { value: 'CLIENTE', label: 'Cliente' }
  ];

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white mb-2">
              Gerenciar Usuários
            </h1>
            <p className="text-sm text-gray-500">
              Gerencie todos os usuários do sistema
            </p>
          </div>
          <div className="flex items-center gap-2">
            <Button
              variant="primary"
              onClick={() => {
                setSelectedUsuario(null);
                setIsModalOpen(true);
              }}
            >
              Novo Usuário
            </Button>
          </div>
        </div>

        <div className="mt-6 flex flex-col sm:flex-row items-center gap-4">
          <div className="flex-1 w-full sm:max-w-xs">
            <Input
              type="text"
              placeholder="Buscar usuários..."
              value={searchTerm}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => setSearchTerm(e.target.value)}
              className="w-full"
            />
          </div>
          <div className="flex flex-wrap gap-3">
            <Select
              placeholder="Função"
              options={roleOptions}
              value={roleOptions.find(opt => opt.value === roleFilter)}
              onChange={(option) => setRoleFilter(option ? (option as any).value : '')}
              isClearable
              className="whitespace-nowrap min-w-40"
            />
          </div>
        </div>
      </div>

      <div className="flex justify-between mb-4 gap-2 flex-wrap">
        <div className='flex items-center gap-0.5'>
          <Label className='flex items-center mb-0 h-full'>Quantidade por página</Label>
          <Input
            className='max-w-24'
            defaultValue={itemsPerPage}
            max={100}
            min={1}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              const newValue = Number(e.target.value) || 9;
              setItemsPerPage(newValue);
              setCurrentPage(1);
            }}
          />
        </div>
        <Pagination
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={setCurrentPage}
        />
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {usuarios.length > 0 ? (
            usuarios.map((usuario) => (
              <UsuarioCard
                key={usuario.id}
                usuario={usuario}
                onEdit={() => handleEdit(usuario)}
                onDelete={() => handleDelete(usuario.id)}
                onView={() => handleView(usuario)}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhum usuário encontrado
              </p>
            </div>
          )}
        </div>
      )}

      {totalPages > 1 && (
        <div className="mt-6">
          <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={currentPage}
              totalPages={totalPages}
              onPageChange={setCurrentPage}
            />
          </div>
        </div>
      )}

      <Modal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedUsuario(null);
        }}
        className="max-w-[800px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {selectedUsuario ? 'Editar Usuário' : 'Novo Usuário'}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {selectedUsuario 
                ? 'Atualize as informações do usuário.' 
                : 'Preencha os dados para criar um novo usuário.'
              }
            </p>
          </div>
          <UsuarioForm
            usuario={selectedUsuario}
            onSubmit={handleSave}
            onCancel={() => {
              setIsModalOpen(false);
              setSelectedUsuario(null);
            }}
          />
        </div>
      </Modal>
    </div>
  );
}