import React from 'react';
import { formatDate } from '@/utils/format';
import { User } from '@/services/api/users.service';
import Badge from '../ui/badge/Badge';
import ButtonCard from '../ui/button/ButtonCard';

type UsuarioCardProps = {
  usuario: User;
  onEdit: () => void;
  onDelete: () => void;
  onView: () => void;
};

const getRoleColor = (role: string) => {
  switch (role) {
    case 'SUPERADMIN': return 'primary';
    case 'ADMIN': return 'info';
    case 'GESTOR': return 'success';
    case 'VENDEDOR': return 'warning';
    case 'CLIENTE': return 'error';
    default: return 'info';
  }
};

const getRoleText = (role: string) => {
  switch (role) {
    case 'SUPERADMIN': return 'Super Admin';
    case 'ADMIN': return 'Admin';
    case 'GESTOR': return 'Gestor';
    case 'VENDEDOR': return 'Vendedor';
    case 'CLIENTE': return 'Cliente';
    default: return role;
  }
};

const UsuarioCard: React.FC<UsuarioCardProps> = ({
  usuario,
  onEdit,
  onDelete,
  onView
}) => {
  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-4">
      <div>
        <div className="flex justify-between items-start mb-3">
          <div className="flex-1">
            <div className="flex items-center gap-2 flex-wrap overflow-x-hidden">
              <h3 className="font-medium text-gray-900 dark:text-white">
                {usuario.nome}
              </h3>
              <Badge
                color={getRoleColor(usuario.role)}
                className="capitalize"
              >
                {getRoleText(usuario.role)}
              </Badge>
            </div>
            <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
              Email: {usuario.email}
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Celular: {usuario.celular || 'Não informado'}
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Criado em: {formatDate(usuario.createdAt)}
            </p>
            {usuario.empresa && (
              <p className="text-sm text-gray-500 dark:text-gray-400">
                Empresa: {usuario.empresa.nome}
              </p>
            )}
          </div>
          <div className="flex gap-2">
            {/* <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onView}
              title="Visualizar"
              icon="eye"
            /> */}
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onEdit}
              title="Editar"
              icon="edit"
            />
            {usuario.role !== 'SUPERADMIN' && (
              <ButtonCard
                variant="secondary"
                size="xs"
                onClick={onDelete}
                title="Excluir"
                icon="delete"
              />
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default UsuarioCard;
